import React, { Component } from "react";
import M from "materialize-css";
import "./style.css";
import "materialize-css/dist/css/materialize.min.css";
import { BASE_URL } from "../../config/dev";

class ModalDetalhesResp extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    // console.log("new props");
    // console.log(this.props);
    const options = {
      onOpenStart: () => {
        // console.log("Open Start");
      },
      onOpenEnd: () => {
        // console.log("Open End");
      },
      onCloseStart: () => {
        // console.log("Close Start");
      },
      onCloseEnd: () => {
        // console.log("Close End");
      },
      inDuration: 250,
      outDuration: 250,
      opacity: 0.5,
      dismissible: false,
      startingTop: "4%",
      endingTop: "10%"
    };
    M.Modal.init(this.Modal, options);
  }

  render() {
    return (
      <>
        <a title="DETALHES"
          className="waves-effect waves-light btn modal-trigger blue btn-margin-left"
          data-target={`modal_resp_${this.props.details.id}`}
        >
          <i class="material-icons">gradient</i>
        </a>

        <div
          ref={Modal => {
            this.Modal = Modal;
          }}
          id={`modal_resp_${this.props.details.id}`}
          className="modal"
        >
          <div className="modal-content">
            <h4>Detalhes do responsável {this.props.details.nome_resp1}</h4>
            <ul>
              <li>
                <b>CPF: </b>
                {this.props.details.cpf}
              </li>
              <li>
                <b>Senha: </b>
                {this.props.details.senha}
              </li>
              <li>
                <b>Responsável (1): </b>
                {this.props.details.nome_resp1}
              </li>
              <li>
                <b>Responsável (2): </b>
                {this.props.details.nome_resp2}
              </li>
              <li>
                <b>Telefone (1): </b>
                {this.props.details.tel1}
              </li>
              <li>
                <b>Telefone (2): </b>
                {this.props.details.tel2}
              </li>
              <li>
                <b>Aluno: </b>
                {this.props.details.nome_aluno}
              </li>
              <li>
                <b>Matrícula: </b>
                {this.props.details.matricula_aluno}
              </li>
              <li>
                <b>Turma: </b>
                {this.props.details.turma_aluno}
              </li>
              <li>
                <b>Ano letivo: </b>
                {this.props.details.ano_letivo_aluno}
              </li>
            </ul>
            {/* <form
              className="row"
              method="post"
              action={`${BASE_URL}pais/postPais.php`}
            >
              <div class="input-field col s12">
                <textarea
                  id="textarea1"
                  class="materialize-textarea"
                ></textarea>
                <label for="textarea1">Textarea</label>
              </div>
            </form> */}
          </div>
          <div class="modal-footer">
            <a className="modal-close waves-effect waves-red btn-flat">
              CANCELAR
            </a>
            <a className="modal-close waves-effect waves-green btn-flat">
              CONFIRMAR
            </a>
          </div>
        </div>
      </>
    );
  }
}

export default ModalDetalhesResp;
